.content {
    position: relative;
   figure {
       position:relative;
        width: 100%;
        height: 300px;
        background: #efefef;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
            top: 0;
            left: 0;
        }
   }

    >.inner {
        width: 1180px;
        margin: 0 auto;
        position: absolute;
        top: 30%;
        left: 15%;
        opacity: 0;
        transform: translateY(50px);
        transition: 0.5s;
        
        h1 {
            text-align: left;
            position: absolute;
            left: 70px;
            font-weight: 700;
            font-size: 40px;
            color: transparent;
            -webkit-text-stroke: 2px #A6394F;
            margin-bottom: 40px;
            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 55px;
                left: 0;
                width: 40px;
                height: 2px;
                background: #A6394F;
            }
        }

        p {
            text-align: left;
            font-size: 18px;
            color: #A6394F;
            position: absolute;
            left: 70px;
            top: 80px;
        }
    }

    &.on {
        >.inner {
            opacity: 1;
            transform: translateY(0);
        }
    }
    
}

@media screen and (max-width : $tablet) {
    .content {
      
    
        >.inner {
            width: 100%;
            left: -5%;
            opacity: 0;
    }
}
}
